<template>
  <div class="registerManage">
    <!--  -->
    <div class="filter-container">
      <el-input placeholder="">
      </el-input>

      <el-button type="primary" icon="search"></el-button>

      <el-button type="primary" icon="document" @click="handleDownload"></el-button>
    </div>
    <!--  -->
    <el-table :data="patients" border style="width: 100%; text-align:left">
      <el-table-column prop="number" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="" label=""></el-table-column>
      <el-table-column prop="" label=""></el-table-column>
      <el-table-column prop="disease" label=""></el-table-column>
      <el-table-column prop="doctor" label=""></el-table-column>
      <el-table-column prop="date" label=""></el-table-column>
      <el-table-column prop="" label=""></el-table-column>
      <el-table-column prop="" label=""></el-table-column>
      <el-table-column label="" width="180">
        <template scope="scope">
          <el-button type="primary" class="btn" @click="checkDetail(scope.$index)"></el-button>
          <el-button type="danger" class="btn" @click="deleteDetail(scope.$index,patients)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" :before-close="handleClose">
      <ul class="patientsdetail">
        <li><h5></h5><span>{{patientsdetails.number}}</span></li>
        <li><h5>:</h5><span>{{patientsdetails.name}}</span></li>
        <li><h5>:</h5><span></span></li>
        <li><h5></h5><span></span></li>
        <li><h5>:</h5><span>{{patientsdetails.sex}}</span></li>
        <li><h5>:</h5><span>{{patientsdetails.age}}</span></li>
        <li><h5>:</h5><span>{{patientsdetails.position}}</span></li>
        <li><h5>:</h5><span>{{patientsdetails.disease}}</span></li>
        <li><h5>:</h5><span>{{patientsdetails.doctor}}</span></li>
        <li><h5>:</h5><span>{{patientsdetails.date}}</span></li>
        <li><h5>:</h5><span></span></li>
        <li><h5>:</h5><span></span></li>
      </ul>
      <h5 slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </h5>
    </el-dialog>

  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        patients: [],
        dialogVisible: false,
        patientsdetails: ''
      };
    },
    mounted () {
      let registerThis = this;

      registerThis.$http.get(api.patientList).then((response) => {
        // response
        console.log(registerThis.patients, response);
        // json
        registerThis.patients = response.data.tableData;
      }, response => {
        // error callback
        alert('');
      });
    },
    methods: {
      // dialog
      handleClose (done) {
        this.$confirm('').then(_ => {
          done();
        }).catch(_ => {});
      },
      // 
      checkDetail (index) {
        this.dialogVisible = true;
        this.patientsdetails = this.patients[index];
      },
      deleteDetail (index, rows) {
        rows.splice(index, 1);
      },
      // excel
      handleDownload () {
        var vm = this;

        require.ensure([], () => {
          const { export_json_to_excel } = require('vendor/Export2Excel');
          const tHeader = ['', '', '', '', '', '', '', '', ''];
          const filterVal = ['number', 'name', '', '', 'disease', 'doctor', 'date', '', ''];
          const patients = vm.patients;
          const data = vm.formatJson(filterVal, patients);
          export_json_to_excel(tHeader, data, 'excel');
        });
      },
      formatJson (filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]));
      }
    }
};
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .registerManage .filter-container
    padding-bottom:30px
    .el-input
      width: 400px
  .registerManage .el-table-column
    font-weight: bold
  .registerManage .patientsdetail
    h5
      display: inline-block
      width: 100px
      line-height: 30px
      font-weight: bold
  .registerManage .btn
      display: inline-block
</style>



