<template>
  <div class="innnerApp">
    <div class="app-body">
      <div class="menu" :class="{ smallNav: isCollapse }">
        <el-menu default-active="1-4-1" class="el-menu-vertical-demo"  :collapse="isCollapse"  theme="dark">
          <el-menu-item index="1"  @click="navToggle">
            <template>
              <i :class="{ 'icon-packUp': !isCollapse ,'icon-spread': isCollapse }"></i>
              <span slot="title">/</span>
            </template>
          </el-menu-item>
          <el-submenu index="2" v-if="type === '01' || type === '03'">
            <template slot="title">
              <i class="icon-nurse"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <span slot="title"></span>
              <el-menu-item index="2-1"><router-link to="addNurse"></router-link></el-menu-item>
              <!---->
              <el-menu-item index="2-2"><router-link to="hasNurse"></router-link></el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="">
              <el-menu-item index="2-3"><router-link to="shift"></router-link></el-menu-item>
              <el-menu-item index="2-5"><router-link to="turnOver"></router-link></el-menu-item>
              <el-menu-item index="2-6"><router-link to="complainInfo"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3" v-if="type === '01' || type === '02'">
            <template slot="title">
              <i class="icon-doctor"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <span slot="title"></span>
              <el-menu-item index="3-1"><router-link to="addDoctor"></router-link></el-menu-item>
              <el-menu-item index="3-2"><router-link to="editDoctor"></router-link></el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="">
              <el-menu-item index="3-3"><router-link to="shiftDoctor"></router-link></el-menu-item>
              <el-menu-item index="3-4"><router-link to="staffFlow"></router-link></el-menu-item>
              <el-menu-item index="3-5"><router-link to="officeComplaints"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="4" v-if="type === '01' || type === '04'">
            <template slot="title">
              <i class="icon-checkstand"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <span slot="title"></span>
              <el-menu-item index="4-1"><router-link to="register"></router-link></el-menu-item>
              <el-menu-item index="4-2"><router-link to="registerManage"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="5" v-if="type === '01' || type === '05'">
            <template slot="title">
              <i class="icon-pharmacy"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <span slot="title">
                <i class="icon-chineseMedicine"></i>
              </span>
              <el-menu-item index="5-1"><router-link to="prescriptionManageC"></router-link></el-menu-item>
              <el-menu-item index="5-2"><router-link to="drugInfosC"></router-link></el-menu-item>
              <el-menu-item index="5-3"><router-link to="drugApplyC"></router-link></el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
              <span slot="title">
                <i class="icon-WesternMedicine"></i>
              </span>
              <el-menu-item index="5-4"><router-link to="prescriptionManageW"></router-link></el-menu-item>
              <el-menu-item index="5-5"><router-link to="drugInfosW"></router-link></el-menu-item>
              <el-menu-item index="5-6"><router-link to="drugApplyW"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="6" v-if="type === '01' || type === '06'">
            <template slot="title">
              <i class="icon-DrugStorage"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="6-1"><router-link to="drugStorage"></router-link></el-menu-item>
              <el-menu-item index="6-2"><router-link to="checkDrugStorage"></router-link></el-menu-item>
              <el-menu-item index="6-3"><router-link to="stockRemoval"></router-link></el-menu-item>
              <el-menu-item index="6-3"><router-link to="trashy"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="7">
            <template slot="title">
              <i class="icon-patient"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="7-1" v-if="type === '01'  || type === '04'"><router-link to="addPatient"></router-link></el-menu-item>
              <el-menu-item index="7-2"><router-link to="hasPatient"></router-link></el-menu-item>
              <el-menu-item index="7-3"><router-link to="hasRegistered"></router-link></el-menu-item>
              <el-menu-item index="7-4" v-if="type === '01' || type === '02'"><router-link to="addHospitalized"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="8">
            <template slot="title">
              <i class="icon-hospital"></i>
              <span slot="title"></span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="8-1"><router-link to="hisIntroduction"></router-link></el-menu-item>
              <el-menu-item index="8-2"><router-link to="staffIntroduction"></router-link></el-menu-item>
              <el-menu-item index="8-3"><router-link to="patientCount"></router-link></el-menu-item>
              <el-menu-item index="8-4"><router-link to="patientFeedback"></router-link></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </div>
      <div class="content" @mouseover="navColse">
      <v-header></v-header>
      <div class="tagcontent">
        <transition name="HISshow">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import header from '../header/header.vue';
  export default {
    name: 'app',
    data () {
      return {
        isCollapse: true,
        userLogin: '',
        type: ''
      };
    },
    methods: {
      navToggle () {
        this.isCollapse = !this.isCollapse;
      },
      navColse () {
        this.isCollapse = true;
      }
    },
    created: function () {
      if (sessionStorage.getItem('easeHis')) {
        this.userLogin = true;
        this.type = sessionStorage.getItem('easeHisType');
        console.log(this.type);
      } else {
        this.userLogin = false;
      }
    },
    components: {
      'v-header': header
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.innnerApp
  background-color:#fff
  min-height: 800px
</style>



