<template>
  <div>
    <h3 style="margin-bottom:20px;"></h3>
    <div style="margin:20px 0px;">
      <el-input v-model="input" placeholder="" style="width:195px;"></el-input>
      <el-select v-model="select" placeholder="" style="width:150px;">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="2"></el-option>
        <el-option label="" value="3"></el-option>
      </el-select>
      <el-button type="primary" icon="search"></el-button>
    </div>
    <el-table :data="tableData" border style="width:100%;">
      <el-table-column fixed label="" prop="date" width="150"></el-table-column>
      <el-table-column label="" prop="number" width="150"></el-table-column>
      <el-table-column label="" prop="name" width="150"></el-table-column>
      <el-table-column label="" prop="age" width="150"></el-table-column>
      <el-table-column label="" prop="phone" width="200"></el-table-column>
      <el-table-column label="" prop="symptoms" width="200"></el-table-column>
      <el-table-column label="" prop="disease" width="200"></el-table-column>
      <el-table-column label="" fixed="right" prop="position" width="250"></el-table-column>
    </el-table>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue';
import {api} from '../../../global/api.js';
export default {
  data () {
    return {
      tableData: [],
      input: '',
      select: ''
    };
  },
  created () {
    let me = this;
    Vue.http.get(api.patientList).then(function (response) {
      console.log(response);
      console.log(':', response.tableData);
      me.tableData = response.data.tableData;
    }, function (response) {
      alert('');
    });
  }
  // methods: {
  //   getRegisteredData () {
  //     let me = this;
  //     Vue.http.get('../../static/patientList.json').then(function (response) {
  //       console.log(response);
  //       console.log(':', response.tableData);
  //       me.tableData = response.data.tableData;
  //     }, function (response) {
  //       alert('');
  //     });
  //   }
  // }
};
</script>


